  searchFormAdvanced: FormGroup = new FormGroup({
    address: new FormControl(''),
    numberRoom: new FormControl(''),
    upperBound: new FormControl(''),
    lowerBound: new FormControl(''),
  });
  sub: Subscription;
  query: string = '';
  currentUser: UserToken;
  listDocument = [];
  page = 1;
  pageSize = 9;
  isSelected = true;
