  constructor(private categoryService: CategoryService,
              private activatedRoute: ActivatedRoute,
              private documentService: DocumentService,
              private authenticationService: AuthenticationService,
              private router: Router) {
    this.sub = this.activatedRoute.queryParams.subscribe(params => {
      this.query = params.address;
      if (this.query != null) {
        this.searchForm.value.name = this.query;
        this.searchFormAdvanced.value.address = this.query;
        // this.search();
      } else {
        // this.getAllHouse();
      }
    });
    this.authenticationService.currentUser.subscribe(value => {
      this.currentUser = value;
    });
  }
  viewDetail(id:any){
    this.router.navigateByUrl("/document/" + id);
  }
  fileUrl = environment.apiUrl;
  getAllDocument(){
    this.documentService.getAll().subscribe(res => {
      this.listDocument = res;
    });
  }
  ngOnInit() {
    this.getAllDocument();
    $(document).ready(function() {
      $('.latest-product__slider').owlCarousel({
        loop: true,
        margin: 0,
        items: 1,
        dots: false,
        nav: true,
        navText: ['<span class=\'fa fa-angle-left\'><span/>', '<span class=\'fa fa-angle-right\'><span/>'],
        smartSpeed: 1200,
        autoHeight: false,
        autoplay: true
      });
      $('.hero__categories__all').on('click', function() {
        $('.hero__categories ul').slideToggle(400);
      });
      var rangeSlider = $('.price-range'),
        minamount = $('#minamount'),
        maxamount = $('#maxamount'),
        minPrice = rangeSlider.data('min'),
        maxPrice = rangeSlider.data('max');
      rangeSlider.slider({
        range: true,
        min: minPrice,
        max: maxPrice,
        values: [minPrice, maxPrice],
        slide: function(event, ui) {
          minamount.val('$' + ui.values[0]);
          maxamount.val('$' + ui.values[1]);
        }
      });
      minamount.val('$' + rangeSlider.slider('values', 0));
      maxamount.val('$' + rangeSlider.slider('values', 1));
    });
  }

